}

/* Fancy blur effect */
#loading-bar .peg {
  -moz-box-shadow: #777777 1px 0 60px 1px !important;
  -ms-box-shadow: #777777 1px 0 60px 1px !important;
  -webkit-box-shadow: #777777 1px 0 60px 1px !important;
  box-shadow: #777777 1px 0 60px 1px !important;
}

$disable-color: rgba(140, 0, 0, 0.3);

.row-disable, .row-disable:hover {
  background: $disable-color !important;
}

@mixin transition($effect, $duration) {
  -webkit-transition: all $duration $effect;
  -moz-transition: all $duration $effect;
  -ms-transition: all $duration $effect;
  -o-transition: all $duration $effect;
  transition: all $duration $effect;
}

.div-pointer{
  cursor: pointer !important;
  @include transition(ease,.3s);
}

table tbody > tr {
  @include transition(ease,.3s);
}

.div-pointer:hover, table tbody tr:hover{
  background-color: rgba(0,0,0,0.04);
  -webkit-transform: scale3d(1, 1, 1);
  -moz-transform: scale3d(1, 1, 1);
  -ms-transform: scale3d(1, 1, 1);
  -o-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
  opacity: 1;
  filter: alpha(opacity=100);
}

table tbody tr[tabindex="0"] {
  border-left: 10px #777777 solid;
  background-color: rgba(0,0,0,0.04);
}

$desc-hover-color: #2196F3;

.descriptionComplete {
  @include transition(ease,.3s);
  width: 450px;
  white-space: normal;
}

.descriptionShort {
  @include transition(ease,.3s);
  width: 450px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.descriptionShort:hover, .descriptionComplete:hover {
  color: $desc-hover-color;
}

// ui-view transitions

.ui-view-container {
  position: relative;
}

[ui-view].ng-enter.crud-panel, [ui-view].ng-leave.crud-panel {
  @include transition(ease-in-out,.5s);
  position: absolute;
  left: 0;
  right: 0;

}


[ui-view].ng-enter.crud-panel {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
  transform: translate3d(100%, 0, 0);
}


[ui-view].ng-enter-active.crud-panel {
  opacity: 1;
  -webkit-transform: none;
  transform: none;
}

[ui-view].ng-leave.crud-panel {
  opacity: 1;
}

[ui-view].ng-leave-active.crud-panel {
  opacity: 0;
  -webkit-transform: translate3d(100%, 0, 0);
  transform: translate3d(100%, 0, 0);
}

